import 'core-js/stable'
import React, { useEffect, useState } from 'react'
import 'regenerator-runtime/runtime'
import Loader from 'components/Common/Loader'
import { HashRouter as Router, Switch, Route } from 'react-router-dom'
import "./app.css";

const Game = React.lazy(() => import('components/Game'))
const Dashboard = React.lazy(() => import('components/Dashboard'))
const Report = React.lazy(() => import('components/Dashboard/AnalyticsReport/index'))
const LoginScreen = React.lazy(() => import('components/Dashboard/LoginScreen'))

function getWindowDimensions() {
  const { innerWidth: width, innerHeight: height } = window;
  return {
    width,
    height
  };
}

export function useWindowDimensions() {
  const [windowDimensions, setWindowDimensions] = useState(getWindowDimensions());

  useEffect(() => {
    function handleResize() {
      setWindowDimensions(getWindowDimensions());
    }

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  return windowDimensions;
}

const App = () => {
  return (
    <React.Suspense fallback={<Loader />}>
      <Router>
        <Switch>
          <Route exact path='/Login'>
            <LoginScreen />
          </Route>
          <Route exact path='/Dashboard'>
            <Dashboard />
          </Route>
          <Route exact path='/:instanceId/Report'>
            <Report />
          </Route>
          <Route exact path='/:instanceId/'>
            <Game />
          </Route>
          <Route exact path='/'>
            <Game />
          </Route>
        </Switch>
      </Router>
    </React.Suspense>
  )
}

export default App